  addforms(){
    const urlPattern = /^(https?:\/\/)?([a-z0-9-]+\.)?[a-z0-9-]+\.[a-z]{2,}\/?.*$/i;

    interface FormGroupConfig {
      [controlName: string]: [any, ValidatorFn[]];
    }

    let timer = this.formBuilder.group(
      {URL: ['', Validators.pattern(urlPattern)],
      hours:['', [Validators.required, Validators.min(0)]],
      minutes:['', [Validators.required, Validators.min(0), Validators.max(59)]],
      seconds:['', [Validators.required, Validators.min(0), Validators.max(59)]]
      } 
    )
      
    // const timerJson = JSON.stringify(timer.value);
    this.newforms.push(timer);

  }

