///<reference types="chrome"/>

import { Component, OnInit } from '@angular/core';

import { AuthService } from '../_services/auth.service';
import { StorageService } from '../_services/storage.service';
import {Router, ActivatedRoute} from '@angular/router'; 
import {HttpClient, HttpResponse} from '@angular/common/http';
import { FormBuilder, FormControl, FormGroup, 
  Validators } from '@angular/forms';


@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent  {
  loginForm!:FormGroup
  
  isSubmitted = false;
  isLoggedIn = false;
  isLoginFailed = false;
  errorMessage = '';
  roles: string[] = [];

  constructor(private authService: AuthService, 
    private storageService: StorageService, 
    private route:ActivatedRoute, 
    private router: Router, 
    private formbuilder:FormBuilder) { }

  ngOnInit(): void {
    this.loginForm = this.formbuilder.group({
      username: new FormControl('', Validators.required),
      password:new FormControl('', Validators.required)
    });
    
  }
  get fc() {
    return this.loginForm.controls;
  }


  sendMessageToExtension(extensionId:any,message: any) {
    if (typeof chrome !== 'undefined' && chrome.runtime) {
      chrome.runtime.sendMessage(extensionId,message, (response) => {
        // console.log('Received response from background script:', response);
      });
    } else {
      console.error('Chrome runtime is not available.');
    }
  }


  Onsubmit(): void {
    // const { username, password } = this.form;
    
  const username = this.loginForm.get('username')?.value;
  const password = this.loginForm.get('password')?.value
  
  if (username && password){
    this.authService.login(
      username, password
    ).subscribe(
      {

      next: Response => {
        console.log(Response.body);
        const token = Response.body.token;
        const username = Response.body.username;
        this.storageService.saveUser(Response);
        window.sessionStorage.setItem('token',token);
        window.localStorage.setItem('token', token);
        window.sessionStorage.setItem('username',username);
        window.localStorage.setItem('username',username);
        console.log(window.sessionStorage);
        // chrome.storage.local.set({ token: token, username: username }, () => {
        //   console.log('Auth details saved in Chrome Storage');
        // });
        console.log(chrome);
        this.sendMessageToExtension('bdnkfkcdglljacbjaagbhmdoppfcadmb',{action:'username',data:username});
        // console.log(ID);
        console.log(this.route.snapshot);
        this.isLoginFailed = false;
        this.isLoggedIn = true;
        this.roles = this.storageService.getUser().roles;
        console.log('nice!');
        this.router.navigate(['/profile', username]);
        
        // this.router.navigate(['register'], { relativeTo: this.route })  
        
      },
      error: err => {
        // this.errorMessage = err.error.message;
        this.isLoginFailed = true;
        console.log('error');
        console.log(username);
      },
      
    });

  }
  else{
    this.isLoginFailed = true;
    this.isLoggedIn = false;
    console.log('error');
    console.log(username);
  }
      }

  // profile_redirect(): void{
  //   if (this.isLoggedIn = true){
  //     this.router.navigate([`/profile`,this.fc['username'].value]);
  //     console.log("checking...");
  //   }
  // }

  

  reloadPage(): void {
    window.location.reload();
  }
}